@import 'global.scss';

:host {
  /**
  * @prop --auto-layout-gap: The gap between the auto layout elements.
  * @prop display: The display mode of the host element.
  */
  --auto-layout-gap: #{$rwc-spacing--xs};
  display: block;
}

.container {
  display: flex;
  gap: var(--auto-layout-gap);
  flex-wrap: wrap;
}
